.h-wrapper
{
    max-width: 1440px;
    width: 90%;
    margin: auto;
   
}
.navbar
{
    padding: 10px 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
   
}

.nav-items
{
list-style: none;
display: flex;
}
.navbar .nav-items li{
    
    padding: 10px 15px;
    font-size: 18px;
    font-weight: 400;
    color: var(--black);

    margin-right: 35px;

}
.navbar .nav-items li:nth-child(4)
{
    background-color: #cfa152;
    color: #fff;
    border: 1px solid #cfa152;
    transition: all 0.5s ease-in-out;
    margin-right: 0px;
    
}
.navbar .nav-items li:nth-child(4):hover
{
  
    background-color: #fff;
    color: var(--black);
    border: 1px solid #cfa152;
}
.mobile-menu-icon
{
    display: none;
}
@media screen and (max-width : 780px)
{
    .nav-items{
        display: none;
       
    }
    .mobile-menu-icon
{
    display: block;
    border: none;
    color: #cfa152;
    font-size: 28px;
    background-color: #fff;
}
.mobile-nav-items
{
    padding:15px 0px;
    position: absolute;
    display: block;
    list-style: none;
    text-align: center;
    line-height: 2.5rem;
    color: var(--black);
    left: 0;
    top: 75px;
    transition: all 10s ease-out;
    width: 100%;
    box-shadow: rgba(255, 255, 255, 0.1) 0px 1px 1px 0px inset, rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
    
}


}