#about
{
  
    background:linear-gradient(0deg, rgba(0, 0, 0, 0.788), rgba(0, 0, 0, 0.829)), url('../../assets/hero.jpg');
    height: 70vh;
    text-align: center;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-attachment: fixed;
}
#about h1{
    color: #fff;
    font-size: 36px;
    font-weight: bold;

}
#about>p{
    font-size: 18px;
    color: #fff;
    width: 80%;
    line-height: 2.5rem;

}