
.card
{
    width: 30%;
    margin: 10px;
    overflow: hidden;
    box-shadow: 0 4px 8px 0 #0003, 0 6px 20px 0 #0000;

}
.card p{
    letter-spacing: 1px;
    margin-top: 15px;
    font-size: 16px;
    font-weight: 700;
    color: #895f18;
    padding-left: 15px;
}
.card h2{
    font-size: 24px;
    margin-top: -10px;
    font-weight: bold;
    color: #000;
    padding-left: 15px;
}
.card span{
    padding-left: 15px;

}
.card svg{
    margin-right: 10px;
}
.card h3{
    position: relative;
    padding-left: 15px;
    top: -160px;
    color: #fff;
    font-weight: bold;
}

.newl{
    background-color: #212211;
    text-align: center;
    color: #fff;
    font-size: 18px;
    padding: 5px;
    width: 40%;
    position: absolute;
    right: 3%;
    /* left: 0; */
    top: 5%;
}
.bhk
{
    position: absolute;
    background-color: #49330e;
    left: 3%;
    top: 5%;
    padding: 8px;
    color: #fff;
    font-size: 16px;
    font-weight: 700;
}
.carousel-control-next-icon

{
    background-color: #cfa152;
    opacity: 1.5;
    position: absolute;
    top: 0;
    right: 3%;
    box-shadow: 0 4px 8px 0 #0003, 0 6px 20px 0 #0000;
    cursor: pointer;
}
.carousel-control-prev-icon
{
    background-color: #cfa152;
    opacity: 1.5;
    position: absolute;
    top: 0;
    right:7%;
    box-shadow: 0 4px 8px 0 #0003, 0 6px 20px 0 #0000;
    cursor: pointer;
}
.hr
{
    background-color: #895f18;
    width: 10%;
    height: 1vh;
    border-radius: 5px;
margin: auto;
}
