#hero-wrapper
{
    max-width: 1440px;
    width: 90%;
    margin: auto;
}
.hero-row
{
    margin-top: 40px;
    display: flex;
    justify-content: space-between;
    align-self: center;

    
}
.hero-right
{
    text-align: right;
}
.hero-row img
{
    width: 85%;
    border-radius: 300px 300px 0px 0px;
    box-shadow: 0 4px 8px 0 #0003, 0 6px 20px 0 #0000;
}
.hero-left{
    align-self: center;
 
}
.hero-left h1{
    font-size: 36px;
    line-height: 3.5rem;
    font-weight: bold;
}
.hero-row p{
    font-size: 18px;
    line-height: 2rem;
    color: var(--black);
}
.hero-form{
    width: 80%;
    background-color: #fff;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    margin-top: 20px;
        border-radius: 50px;
}
.hero-form input
{
    outline: none;
    font-size: 18px;
    color: black;
    width: 75%;
    border: none;
    padding: 15px;
    border-radius: 50px;
}
.hero-form button
{
    width: 25%;
    font-size:18px;
  
    border: none;
    padding: 15px;
    border-radius: 50px;
    color: #fff;
    background-position: 0%;
    background-repeat: no-repeat;
    background-size: 500px;
    background-image: linear-gradient(30deg, #cfa152 50%, transparent 50%);
    transition: all 0.5s ease-in-out;
}
.hero-form button:hover
{
    background-position: 100%;
background-color:var(--black);
}
.counter
{    
    font-size: 18px;
   transition: all 0.5 ease-in-out;
}
.herro-service
{
    display: flex;
    margin-top: 40px;
}
.herro-service span
{
    color: #cfa152;
    margin-left: 5px;
    font-size: 36px;
    font-weight: bold;
    vertical-align: middle;
}
.counter
{
    font-size: 36px!important;
    font-weight: bold;
    margin-bottom: 10px;
}
.numbercounter
{
    padding: 0px 40px 0px 0px;
    margin-right: 40px;
    text-align: center;
    border-right: 1px solid #dbb87d96;
    text-align: center;
}
.counter-text
{
    font-size: 24px;
    font-weight: 900;
}
.logo img{
    -webkit-filter: grayscale(100%);
    filter: grayscale(100%);
}
.logo img:hover
{
    filter:grayscale(10%) ;
}