.contact
{
   background-image:  url('../../assets/contactbg.webp');
   background-color: #cfa152;
   padding: 50px 0px;
}
.contact h1{
    color: #fff;
    font-size: 36px;
    margin: 20px;

}
.contactform
{
    padding: 30px;
    width: 50%;
    background-color: #fff;
}
.contactform input
{
    background-color: transparent;
    border: 1px solid #f0e1c8;;
    margin: 20px 0px;
    padding: 15px 10px;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
form input:focus
{
    outline: none;
}
.contactbutton
{
    padding: 10px;
    background-color: #cfa152;
    width: 20%;
    float: right;
    border: none;
    font-size: 18px;
    color: #fff;
}